<template>
  <div id="music-list" :style="scroll ? {'overflow': 'overlay'}:{'overflow':'hidden'}">
    <div id="list-category" v-if="category">
      <span id="category-number">序号</span>
      <span id="category-name">音乐名称</span>
      <span id="category-artists">歌手</span>
      <span id="category-album">专辑</span>
      <span id="category-duration">时长</span>
      <span id="category-favorites">收藏</span>
    </div>
    <div class="music-item" :class="musicList.indexOf(musicItem)%2===0 ? 'even':'odd'" v-for="musicItem in musicList" :key="musicList.indexOf(musicItem)" @click="handleMusicItemClick(musicItem)">
      <span class="music-item-number">{{musicList.indexOf(musicItem)}}</span>
      <span class="music-item-name">{{musicItem.name}}</span>
      <span class="music-item-artists"><router-link :to="`browser-artists/${musicItem.artists.id}`">{{musicItem.artists.name}}</router-link></span>
      <span class="music-item-album"><router-link :to="`browser-album/${musicItem.album.id}`">{{musicItem.album.name}}</router-link></span>
      <span class="music-item-duration">{{`${parseInt(musicItem.duration/60000)}:${(musicItem.duration-parseInt(musicItem.duration/60000)*60000)/1000}`}}</span>
      <span class="music-item-favorites"><i class="iconfont" :class="musicItem.favorites ? 'icon-favorites-fill':'icon-favorites' "></i></span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'music-list',
  props: {
    musicList: {
      type: Array,
      default: null,
      required: true,
      // validator: function(value) {
      //   return value.length !== 0
      // }
    },
    scroll: {
      type: Boolean,
      default: false,
      required: false
    },
    category: {
      type: Boolean,
      default: false,
      required: false
    }
  },
  methods: {
    handleMusicItemClick: async function(musicItem) {
      console.log(musicItem)
      let play_list = this.$store.getters.getMusicList
      // play_list[play_list.length-1].next = musicItem
      // musicItem.prev = play_list[play_list.length-1]
      // musicItem.next = play_list[0]
      for(let play_item of play_list) {
        if(play_item.id === musicItem.id) {
          this.$store.dispatch('musicInit',musicItem)
          this.$store.dispatch('playMusic')
          return
          // break
        }
      }
      play_list.push(musicItem)
      this.$store.dispatch('musicListInit',play_list)
      console.log(play_list)
      this.$store.dispatch('musicInit',musicItem)
      this.$store.dispatch('playMusic')
      // (async ()=>{this.$store.dispatch('playMusic')})()
      // (function(){this.$store.getters.getAudio.play()})()
    }
  }
};
</script>

<style lang="scss" scoped>
#music-list {
  width: 100%;
  height: 100%;
  font-size: 1vw;
  // background: #ffffff66;
  #list-category {
    display: flex;
    align-items: center;
    width: 100%;
    height: 7%;
    background: #ffffff8c;
    #category-number {
      width: calc(7% - 1vw);
      padding-left: 1vw;
    }
    #category-name {
      width: calc(25% - 1vw);
      padding-left: 1vw;
    }
    #category-artists {
      width: calc(15% - 1vw);
      padding-left: 1vw;
    }
    #category-album {
      width: calc(32% - 1vw);
      padding-left: 1vw;
    }
    #category-duration {
      width: calc(12% - 1vw);
      padding-left: 1vw;
    }
    #category-favorites {
      width: 9%;
      // padding-left: 1vw;
    }
  }
  .odd {
    background: #ccccccab;
  }
  .even {
    background: #c0c0c09e;
  }
  .music-item {
    display: flex;
    align-items: center;
    width: 100%;
    height: 10%;
    cursor: pointer;
    .music-item-album, .music-item-artists, .music-item-duration, .music-item-favorites, .music-item-number, .music-item-name{
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      line-clamp: 1;
    }
    .music-item-number {
      width: calc(7% - 2vw);
      padding-left: 2vw;
      // background: green;
    }
    .music-item-name {
      width: calc(25% - 1vw);
      padding-left: 1vw;
      // background: red;
    }
    .music-item-artists {
      width: calc(15% - 1vw);
      padding-left: 1vw;
      a {
        color: #7d7d7d;
      }
      // background: blue;
    }
    .music-item-album {
      width: calc(32% - 1vw);
      padding-left: 1vw;
      a {
        color: #7d7d7d;
      }
      // background: red;
    }
    .music-item-duration {
      width: calc(12% - 2vw);
      padding-left: 2vw;
      color: #7d7d7d;
      // background: green;
    }
    .music-item-favorites {
      width: calc(9% - 1vw);
      padding-left: 1vw;
      // background: red;
    }
  }
}
</style>